<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有的优惠券信息。</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>优惠券名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <cms-sticky>
      <section class="cms-batch">
        <cms-button v-prem="'/coupon/save'" icon="add" type="success" size="small" @click="$routerLink('/coupon/save','save')">新增</cms-button>
        <cms-button  icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
      </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" 
      @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="name" label="优惠券名称" min-width="180" show-overflow-tooltip class-name="text-overflow"></el-table-column>
      <el-table-column prop="couponType" label="类型" align="center" width="140">
        <div slot-scope="scope">
          <span v-if="scope.row.couponType==1" class="info">活动优惠券</span>
          <span v-if="scope.row.couponType==2" class="success">普通优惠券</span>
        </div>
      </el-table-column>
      <el-table-column prop="discountAmount" label="面值"></el-table-column>
      <el-table-column prop="discountAmount" label="使用条件" width='180'>
        <div slot-scope="scope">
          <span>满{{scope.row.conOrderAmount}}减{{scope.row.discountAmount}}</span>
        </div>
      </el-table-column>
       <el-table-column prop="discountAmount" label="使用期限" min-width="190">
        <div slot-scope="scope">
          <span v-if="scope.row.expirateType==1">{{$moment(scope.row.useBeginTime,'YYYY-MM-DD')}}
            至 
          {{$moment(scope.row.useEndTime,'YYYY-MM-DD')}}</span>
          <span v-if="scope.row.expirateType==2">派发后{{scope.row.useDay}}天</span>
        </div>
      </el-table-column>
      <el-table-column label="操作选项" width='160' fixed="right" align="center">
        <div slot-scope="scope">
          <div v-if="scope.row.canFixed">
             <cms-button icon='edit' size='small' type='info'  v-prem="'/coupon/update'"
           @click="$routerLink('/coupon/update','update',{id:scope.row.id})">修改
          </cms-button>
          <cms-button icon='error' size='small' type='info' 
           @click="deleteData(scope.row.id)">删除
          </cms-button>
          </div>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import { couponPage, couponDelete } from '@/api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  name: '',
  discountAmount: '',
  beginTime: '',
  endTime: '',
  couponType: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(couponDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(couponDelete, ids) // 这个才是真实的删除方法
    }

  },
  created() {
    this.getTableData(couponPage, this.queryParams)
  }
}
</script>

<style>
</style>



